@mixin maxWH {
    width: 100vw;
    height: 100vh;
}
$max-width:100vw;
$bot-height:50px;
$color-black:rgb(12, 12, 12);
$color-backgroud:#f6f5f6;
.App{
    @include maxWH();
    background-color: $color-backgroud;
}
.home-box{
    height: calc(100vh - $bot-height);
    width: $max-width;
    padding-bottom: 10px;
    overflow-y: auto;
}
.home-bot{
    width: $max-width;
    height: $bot-height;
    box-shadow: 0 -1px 2px $color-black;
}