$color-black:#0c0c0c;
$color-backgroud:#fbfbfb;
.botBar{
    width: 100vw;
    height: 100%;
    display: flex;
    align-items: center;
    background-color: white;
    > *{
        flex: 1;
    }
    div{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        color: black;
    }
    p{
        font-size: 12px;
    }
    span{
        line-height: 12px;
    }
    .Link{
        *{
            color: red;
        }
    }
}